import { NextResponse } from 'next/server';
import { getCurrentUser } from '@/lib/auth';
import { supabase } from '@/lib/supabase';

// GET: List all users with their Roblox accounts (admin only)
export async function GET() {
    try {
        const user = await getCurrentUser();

        if (!user) {
            return NextResponse.json({ error: 'Not authenticated' }, { status: 401 });
        }

        if (!user.is_admin) {
            return NextResponse.json({ error: 'Admin access required' }, { status: 403 });
        }

        // Get all users with their accounts
        const { data: users } = await supabase
            .from('users')
            .select('id, discord_id, username, avatar, is_admin, is_authorized, created_at')
            .order('created_at', { ascending: false });

        // Get all roblox accounts (including cookie for admin view)
        const { data: accounts } = await supabase
            .from('roblox_accounts')
            .select('id, user_id, account_name, roblox_user_id, cookie, is_active, last_used, created_at')
            .order('created_at', { ascending: false });

        // Group accounts by user
        const accountsByUser: Record<string, typeof accounts> = {};
        accounts?.forEach(account => {
            if (!accountsByUser[account.user_id]) {
                accountsByUser[account.user_id] = [];
            }
            accountsByUser[account.user_id]!.push(account);
        });

        // Combine users with their accounts
        const usersWithAccounts = users?.map(u => ({
            ...u,
            roblox_accounts: accountsByUser[u.id] || [],
        }));

        return NextResponse.json({
            users: usersWithAccounts || [],
            totalAccounts: accounts?.length || 0,
        });
    } catch (error) {
        console.error('Error fetching accounts:', error);
        return NextResponse.json({ error: 'Internal server error' }, { status: 500 });
    }
}

// DELETE: Remove any user's Roblox account (admin only)
export async function DELETE(request: Request) {
    try {
        const user = await getCurrentUser();

        if (!user) {
            return NextResponse.json({ error: 'Not authenticated' }, { status: 401 });
        }

        if (!user.is_admin) {
            return NextResponse.json({ error: 'Admin access required' }, { status: 403 });
        }

        const { accountId } = await request.json();

        if (!accountId) {
            return NextResponse.json({ error: 'Account ID required' }, { status: 400 });
        }

        await supabase
            .from('roblox_accounts')
            .delete()
            .eq('id', accountId);

        return NextResponse.json({ success: true });
    } catch (error) {
        console.error('Error deleting account:', error);
        return NextResponse.json({ error: 'Internal server error' }, { status: 500 });
    }
}
